.testominalItem {
  min-height: 38.873em;
  max-width: 32.5em;
  position: relative;
  display: inline-block;
}
.testominalItem .testominalContent {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  padding-bottom: 8.313em;
  padding-left: 10px;
  padding-right: 10px;
}
.testominalItem .testominalContent .testominalDesc {
  font-size: 1.563em;
  letter-spacing: 0.2px;
  line-height: 1.8em;
  color: #333333;
  text-align: center;
  font-family: Merriweather Sans;
  font-weight: 300;
  padding-left: 1.25em;
  padding-right: 1.25em;
}
.testominalItem .testominalContent .leftquote {
