  margin-left: 1.75em;
  width: 5.25em;
}
.testominalItem .testominalContent .nameTxt {
  font-size: 1.563em;
  letter-spacing: 0.2px;
  line-height: 1.8em;
  padding-top: 1.92em;
  color: #333333;
  text-align: center;
  font-family: Merriweather Sans;
  font-weight: 700;
}
.swiper-slide .selected {
  display: none;
}
.swiper-slide .notselected {
  display: block;
}
.swiper-slide-active .selected {
